import React from 'react';
import { Row, Col, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import TestimonialCard from '../components/TestimonialCard'; // Asegrate de que la ruta es correcta
import Navbar from '../components/navbar'; // Asegrate de que la ruta es correcta

const LandingPage = () => {
  return (
    <div className="landing-page">
      <div className="opacidad">
      <Navbar/>
      <div className="contenedor">
        {/* Seccin de Bienvenida (Hero Section) */}
        <Row className="hero-section justify-content-center text-center mb-5">
          <Col lg={8}>
            <h1 className="display-1 custom-title tracking-in-expand-forward-bottom">Bienvenido a Uniteach</h1>
            <h3 className="descripcion">Encuentra mentores entre tus compaeros de universidad para mejorar tus habilidades acadmicas</h3>
            {/* Aqu est el botn */}
            <Button 
              as={Link} 
              to="/signup" 
              variant="secondary" 
              size="lg" 
              className="custom-button shadow-drop-center"
            > 
              Comienza Ahora! 
            </Button>
          </Col>
        </Row>

        {/* Features Section */}
        <Row className="features-section mt-5">
          <Col lg={12}>
            <h1 className="display-4 text-white">Caractersticas Principales</h1>
            <ul className="list-unstyled features-text">
              <li>Conecta con mentores expertos en diversas materias.</li>
              <li>Programa clases segn tu disponibilidad.</li>
              <li>Accede a materiales de estudio y recursos tiles.</li>
              <li>Recibe retroalimentacin personalizada en tus reas de estudio.</li>
            </ul>
          </Col>
        </Row>
        {/* Testimonials Section */}
        <Row className="testimonial-card-container justify-content-center">
          <h2 className="text-center text-white">Testimonios de Usuarios</h2>
          <Col xs={12} className="d-flex justify-content-center flex-wrap">
            <TestimonialCard name="Juan Prez" quote="Gracias a Uniteach, he mejorado mis habilidades en matemticas y ahora estoy ms confiado para mis exmenes." />
            <TestimonialCard name="Mara Gutirrez" quote="Encontr a un excelente mentor de programacin que me ayud a entender conceptos difciles de una manera clara y sencilla." />
            <TestimonialCard name="Carlos Paez" quote="Una de las mejores herramientas para el aprendizaje de manera verstil." />
          </Col>
        </Row>
        </div>
    </div>
    </div>
  );
};

export default LandingPage;
