import React from 'react';
import Navbar from '../components/navbar';

export const About = () => {
 

  return (
    <div> 
    <Navbar/>    
      <div className='about'>
        <h1>Acerca de Nosotros</h1>
        <p>Somos Uniteach, una plataforma creada por estudiantes que conecta a otros estudiantes universitarios para ofrecer y recibir mentoras acadmicas.</p>
        <p>Nuestra misin es facilitar el intercambio de conocimientos y promover el xito acadmico de nuestros usuarios.</p>
        {/* Seccin de Contacto (Contact Section) */}
        <section className="contact-section">
          <h2>Contacto</h2>
          <p>Tienes alguna pregunta o comentario? Contctanos!</p>
          <form>
            <input type="text" placeholder="Nombre" />
            <input type="email" placeholder="Correo Electrnico" />
            <textarea placeholder="Mensaje"></textarea>
            <button>Enviar Mensaje</button>
          </form>
        </section>
      </div>
    </div>
  );
};

export default About;
